.login {
  width: 100%;
  height: 100%;
  background-image: url(/static/media/bg-.fc9c090f.jpg);
  background-size: 100% 100%;
}
.login .head {
  height: 80px;
  font-size: 36px;
  line-height: 80px;
}
.login .head .img {
  margin: 0 20px 0 35px;
}
.login .login-content {
  width: 25%;
  height: 25%;
  margin: 150px auto;
}
.login .login-content .login-form-button {
  width: 100%;
}
.login .tips {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  text-align: right;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.login .tips div {
  width: 20%;
  text-align: left;
}
